<template>
  <div class="main quality">
    <div class="main-search">
      <div class="search-item">
        <span class="item-left">开始日期</span>
        <span class="item-right">
         <el-date-picker
           v-model="search_per.beginCalldate"
           type="date"
           value-format="yyyy-MM-dd"
           placeholder="开始日期"
           @change="getSbeginTime">
         </el-date-picker>
         </span>
      </div>
      <div class="search-item">
        <span class="item-left">结束日期</span>
        <span class="item-right">
          <el-date-picker
            v-model="search_per.endCalldate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="结束日期"
            :picker-options="endDatePicker"
            @change="getSendTime">
          </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">外呼团队</span>
        <span class="item-right">
           <el-select v-model="search_per.corp" multiple collapse-tags @change="changeStaff('corp')">
              <template v-for="item in teamData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">品牌</span>
        <span class="item-right">
          <el-select v-model="search_per.proj" multiple collapse-tags @change="changeStaff('proj')">
              <template v-for="item in projData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <button type="button" class="btn-search" @click="search_data">查询</button>
    </div>
    <div class="main-table">
      <div class="table-msg">
        <div class="msg-left">
          <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
        </div>
      </div>
      <el-table
        :data="list.data"
        border
        show-summary
        :summary-method='getTotal'
        style="text-align: center">
        <el-table-column
          prop="calldate"
          align="center"
          :formatter="formatDate"
          label="时间">
        </el-table-column>
        <el-table-column
          prop="crop"
          align="center"
          :formatter="formatCorp"
          label="外呼团队">
        </el-table-column>
        <el-table-column
          prop="porj"
          align="center"
          :formatter="formatProj"
          label="品牌">
        </el-table-column>
        <el-table-column
          prop="leads"
          align="center"
          label="成单数">
        </el-table-column>
        <el-table-column
          prop="seats"
          align="center"
          label="坐席数">
        </el-table-column>
        <el-table-column
          prop="percapita"
          align="center"
          label="人均单量">
        </el-table-column>
      </el-table>
      <pager-table show_size="true" v-bind:cur="search_per.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></pager-table>
    </div>

  </div>
</template>

<script>
import pager from '@/components/pager'
import ajax from "../../config/ajax";
import {format} from '@/config/util'
import Qs from 'qs'
export default {
  name: 'perCapita',
  data () {
    return {
      search_per: {
        beginCalldate: new Date(), // 开始日期
        endCalldate: new Date(), // 结束日期
        corp: [], // 外呼团队
        proj: [], // 品牌团队
        size: 10, // 每页数量
        page: 1 // 页码
      },
      list: {
        data: [], // 数据集
        totalPage: 0, // 总页数
        totalCount: 0 // 总条数
      },
      total: {
        sumleads: '',  //总外呼数
        sumseats: '',    //总接通数
        sumpercapita: '',  //总接通率
      },
      endDatePicker: this.processDate(),  //结束时间不能大于开始时间
      teamData: [],         //外呼团队字典数组
      projData: [],         //品牌字典数组
      isCorpContainAll: true,     //外呼团队是否选则了全选
      isProjContainAll: true,     //品牌是否选则了全选
    }
  },
  created(){
    this.teamData = JSON.parse(localStorage.getItem("cropid"));
    this.projData = JSON.parse(localStorage.getItem("projid"));
    this.search_per.corp[0] = this.teamData[0].id;
    this.search_per.proj[0] = this.projData[0].id;
    this.getPercapitaData()
  },
  methods: {
    // 时间选择器开始时间
    getSbeginTime (val) {
      this.beginTime = val
    },
    // 时间选择器结束时间
    getSendTime (val) {
      this.endTime = val
    },
    processDate(){
      const  self = this;
      return {
        disabledDate(time) {
          if (self.search_per.beginCalldate) {  //如果开始时间不为空，则结束时间大于开始时间
            return new Date(self.search_per.beginCalldate).getTime() >= time.getTime()
          } else {
            // return time.getTime() > Date.now()//开始时间不选时，结束时间最大值小于等于当天
          }
        }
      }
    },
    //初始化数据
    getPercapitaData(){
      var params = {
        beginCalldate:format(this.search_per.beginCalldate, "yyyyMMdd"),
        endCalldate:format(this.search_per.endCalldate, "yyyyMMdd"),
        corp:this.search_per.corp.join(","),
        proj:this.search_per.proj.join(","),
        page:this.search_per.page,
        size:this.search_per.size
      }
      return ajax.request({
        method:'post',
        url:'percapita',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
          params = Qs.stringify(params)
          return params
        }],
        data:params
      }).then((res)=>{
        this.list.data=res.data.list;
        this.list.totalCount=res.data.totalCount;
        this.list.totalPage=res.data.totalPage;
        this.total.sumleads = res.data.sumleads;
        this.total.sumseats = res.data.sumseats;
        this.total.sumpercapita = res.data.sumpercapita;
      }).catch((error)=>{
        console.log(error)
      })
    },
    // 查询
    search_data () {
      if(this.search_per.corp.length == 0){
        this.$message({
          message: '请选择外呼团队',
          type: 'warning'
        });
        return
      }else if(this.search_per.proj.length == 0){
        this.$message({
          message: '请选择品牌',
          type: 'warning'
        });
        return
      }
      this.search_per.page = 1;
      this.getPercapitaData()
    },
    getTotal(param){
      const {columns, data} = param;
      const sums = [];
      columns.forEach((column, index) => {
        const values = data.map(item => Number(item[column.property]));
        if(column.property === 'leads'){
          sums[index] = this.total.sumleads;
        }else if(column.property === 'seats') {
          sums[index] = this.total.sumseats;
        }else if(column.property === 'percapita') {
          sums[index] = this.total.sumpercapita;
        }else{
          sums[index] = '--';
        }
      })
      return sums;
    },
    //格式化日期
    formatDate(row){
      var calldate = row.calldate.toString();
      return row.calldate = calldate.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3");
    },
    //品牌及外呼团队多选
    changeStaff(type){
      if(type == 'corp'){
        if(this.isCorpContainAll){
          this.isCorpContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
          this.search_per.corp.splice(0, 1);// 则删除第一个全部
        }else{
          this.isCorpContainAll = this.search_per.corp.some(value => value === 0)
          if(this.isCorpContainAll){
            this.search_per.corp = [0];
          }else{
            if(this.search_per.corp.length == this.teamData.length-1){
              this.search_per.corp = [0];
              this.isCorpContainAll = true;
            }
          }
        }
      }else if(type == 'proj'){
        if(this.isProjContainAll){
          this.isProjContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
          this.search_per.proj.splice(0, 1);// 则删除第一个全部
        }else{
          this.isProjContainAll = this.search_per.proj.some(value => value === 0)
          if(this.isProjContainAll){
            this.search_per.proj = [0];
          }else{
            if(this.search_per.proj.length == this.projData.length-1){
              this.search_per.proj = [0];
              this.isProjContainAll = true;
            }
          }
        }
      }
    },
    //处理外乎团队的数据
    formatCorp(row){
      for(var i=0;i<this.teamData.length;i++){
        if(row.corp == this.teamData[i].id){
          return row.crop = this.teamData[i].memo;
        }
      }
    },
    //处理品牌的数据
    formatProj(row){
      var projdata = this.projData;
      projdata = projdata.slice(1);
      for(var i=0;i<projdata.length;i++){
        if(row.proj == projdata[i].id){
          return row.porj = projdata[i].memo;
        }
      }
    },
    // 分页
    change_data (index, size) {
      console.log("11111")
      this.search_per.page = index;
      this.search_per.size = size;
      this.getPercapitaData()
    }
  },
  components: {
    'pager-table': pager
  }
}
</script>

<style>
  @import '../../assets/css/pages/main.css';
  .quality{
    padding-left: 15px;
  }
  .quality .el-input{
    width: 150px;
  }
  .el-select-dropdown__wrap ul li{
    padding-left: 20px !important;
    display: block;
  }
  
</style>



